input[type=checkbox].switch-senara {
  position: absolute;
  margin: 8px 0 0 12px;
  width: 3rem;
  height: 2rem;
}
input[type=checkbox].switch-senara + label {
  color: #07392a;
  position: relative;
  padding-left: 6rem;
}
input[type=checkbox].switch-senara + label:before {
  content: "";
  position: absolute;
  display: block;
  left: 0;
  top: 0;
  width: 40px;
  height: 24px;
  border-radius: 16px;
  background: #fff;
  border: 1px solid #d9d9d9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
input[type=checkbox].switch-senara + label:after {
  content: "";
  position: absolute;
  display: block;
  left: 0px;
  top: 0px;
  width: 24px;
  height: 24px;
  border-radius: 16px;
  background: #fff;
  border: 1px solid #d9d9d9;
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
input[type=checkbox].switch-senara + label:hover:after {
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.3);
}
input[type=checkbox].switch-senara:checked + label:after {
  margin-left: 16px;
}
input[type=checkbox].switch-senara:checked + label:before {
  background: #07392a;
}

input[type=checkbox].switch-senara-sm {
  margin: 6px 0 0 10px;
  width: 1.7rem;
  height: 1.3rem;
}
input[type=checkbox].switch-senara-sm + label {
  position: relative;
}
input[type=checkbox].switch-senara-sm + label:before {
  width: 25px;
  height: 15px;
  border-radius: 10px;
}
input[type=checkbox].switch-senara-sm + label:after {
  width: 15px;
  height: 15px;
  border-radius: 10px;
}
input[type=checkbox].switch-senara-sm + label:hover:after {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}
input[type=checkbox].switch-senara-sm:checked + label:after {
  margin-left: 10px;
}

input[type=checkbox].switch-senara-lg {
  margin: 9px 0 0 14px;
  width: 3.5rem;
  height: 2.5rem;
}
input[type=checkbox].switch-senara-lg + label {
  position: relative;
}
input[type=checkbox].switch-senara-lg + label:before {
  width: 50px;
  height: 30px;
  border-radius: 20px;
}
input[type=checkbox].switch-senara-lg + label:after {
  width: 30px;
  height: 30px;
  border-radius: 20px;
}
input[type=checkbox].switch-senara-lg + label:hover:after {
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.3);
}
input[type=checkbox].switch-senara-lg:checked + label:after {
  margin-left: 20px;
}

